@font-face {
  font-family: "Arial";
  font-display: swap;
  src: url("../fonts/Arial-Bold.woff") format("woff"),url("../fonts/Arial-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Arial";
  font-display: swap;
  src: url("../fonts/Arial-Regular.woff") format("woff"),url("../fonts/Arial-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Avenirnextcyr";
  font-display: swap;
  src: url("../fonts/Avenirnextcyr-Regular.woff") format("woff"),url("../fonts/Avenirnextcyr-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"),url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-Bold.woff") format("woff"),url("../fonts/Raleway-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../fonts/Raleway-SemiBold.woff") format("woff"),url("../fonts/Raleway-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

:root {
  --container: 1160px;
  --main-font: Arial, sans-serif;
  --raleway: Raleway, sans-serif;
  --montserrat: Montserrat, sans-serif;
  --avenir-next-cyr: Avenir Next Cyr, sans-serif;
  --font-size: 16px;
  --color-1: #586968;
  --color-2: #f2f6f6;
  --color-3: #bacece;
  --color-4: #103331;
  --color-5: #4C5855;
  --color-6: #2E4846;
  --color-7: #556B69;
  --color-8: #536863;
  --color-9: #485B56;
  --color-10: #305350;
  --color-11: #18544F;
  --color-12: #01524D;
  --color-13: #506A65;
  --color-14: #2B7F77;
  --color-15: #3C8B86;
  --color-16: #ACE3DE;
  --color-17: #D5F2F0;
  --color-18: #CDDCDB;
  --color-19: #E3EAEA;
  --color-20: #FAFAFA;
  --color-21: #B6D1CE;
  --color-22: #8C92AD;
  --color-23: #2B2B2F;
  --color-24: #888888;
  --color-25: #0C6C66;
  --color-26: #0C6D67;
  --color-27: #529B94;
  --white: #fff;
  --bg-color: #F6F7F7;
  --radius-1: 8px;
  --radius-2: 10px;
  --radius-3: 12px;
  --radius-4: 16px;
  --radius-5: 20px;
  --radius-6: 30px;
  --radius-7: 40px;
  --radius-8: 60px;
  --shadow-1: inset 2px 2px 4px 0 rgba(0, 0, 0, 0.15);
  --shadow-2: 0 4px 9px 0 rgba(85, 107, 105, 0.15);
  --shadow-3: 4px 4px 16px 0 rgba(48, 83, 80, 0.08);
  --shadow-4: 0 4px 15px 0 rgba(85, 107, 105, 0.08);
  --gradient-1: linear-gradient(326deg, #01524d 0%, #38dbd2 100%);
  --gradient-2: linear-gradient(130deg, #1d726b 0.8%, #81c1c1 39.9%, #fff 100%);
  --gradient-3: linear-gradient(156deg, #01524d 0%, #38dbd2 100%);
}

html {
  font-size: var(--font-size);
  color: var(--color-1);
  lang: ru;
  font-variant-numeric: lining-nums proportional-nums;
}

html,
body {
  scrollbar-gutter: stable;
  background-color: var(--bg-color);
}

body {
  position: relative;
  max-width: 100vw !important;
  margin: 0;
}

body * {
  font-family: var(--main-font);
  font-weight: 400;
  line-height: 1.4;
}

body.no-scroll {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 3;
  min-height: 55vh;
}

main.layer-up {
  z-index: 5;
}

section {
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  display: block;
  padding: 0;
  margin: 0;
}

strong,
b {
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}

a {
  color: inherit;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

img,
picture,
figure {
  display: block;
  margin: 0;
}

pre {
  margin: 0 !important;
  white-space: break-spaces;
}

button {
  padding: 0;
  border: none;
  outline: none !important;
  background-color: rgba(0,0,0,0);
}

.compensate-for-scrollbar {
  padding-right: 0 !important;
}

.container {
  position: relative;
  max-width: var(--container);
  width: 100%;
  margin: 0 auto;
}

.validate {
  border-color: darkred !important;
}

.main-title,
.main-title * {
  font-family: var(--raleway);
  font-size: 48px;
  font-weight: bold;
  line-height: 1.1;
  user-select: none;
  color: var(--color-4);
}

.second-title,
.second-title * {
  font-size: 40px;
  font-weight: bold;
  line-height: 1.2;
  user-select: none;
}

.third-title,
.third-title * {
  font-family: var(--raleway);
  font-size: 32px;
  font-weight: bold;
  line-height: 1.2;
  user-select: none;
}

.gradient-text {
  background: var(--gradient-1);
  background-clip: text !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: rgba(0,0,0,0) !important;
  color: rgba(0,0,0,0) !important;
}

.gradient-text.gradient-3 {
  background: var(--gradient-3);
}

.text-with-logo,
.text-with-full-logo {
  position: relative;
  display: inline-block;
}

.text-with-logo::before,
.text-with-full-logo::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0) center/contain no-repeat;
}

.text-with-logo {
  text-indent: 64px;
}

.text-with-logo::before {
  width: 64px;
  height: 71px;
  background-image: url("./../img/logo-small.svg");
  transform: translateY(-13px);
}

.text-with-full-logo {
  text-indent: 263px;
}

.text-with-full-logo::before {
  width: 256px;
  height: 82px;
  background-image: url("./../img/logo.svg");
  transform: translateY(-17px);
}

.btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  border: 1px solid rgba(0,0,0,0);
  border-radius: var(--radius-8);
  box-shadow: var(--shadow-1);
  background-color: rgba(0,0,0,0);
  cursor: pointer;
  transition: border-color .3s ease-in-out;
}

.btn::after {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--gradient-1);
  border-radius: var(--radius-8);
  box-shadow: var(--shadow-1);
  opacity: 1;
  transition: opacity .3s ease-in-out;
}

.btn:hover,
.btn:focus-visible {
  border-color: var(--color-1);
}

.btn:hover::after,
.btn:focus-visible::after {
  opacity: 0;
}

.btn:hover .btn-text,
.btn:focus-visible .btn-text {
  color: var(--color-1);
}

.btn:hover .btn-arrow,
.btn:focus-visible .btn-arrow {
  background-color: rgba(0,0,0,0);
}

.btn:hover .btn-arrow::after,
.btn:focus-visible .btn-arrow::after {
  opacity: 1;
}

.btn:hover .btn-arrow .svg-ico,
.btn:focus-visible .btn-arrow .svg-ico {
  color: var(--white);
  transform: rotate(45deg);
}

.btn * {
  position: relative;
  z-index: 1;
}

.btn.with-image {
  gap: 15px;
  padding: 0 10px 0 22px;
}

.btn.with-arrow {
  gap: 10px;
  padding: 0 10px 0 22px;
}

.btn.load {
  position: relative;
  pointer-events: none;
  background: #d8d8d8 !important;
}

.btn.load::after {
  animation-duration: .3s;
  animation-fill-mode: both;
  animation: rotateAnimation .3s linear infinite;
}

.btn.transparent {
  border: 1px solid var(--white);
  background: rgba(0,0,0,0);
}

.btn .btn-text {
  color: var(--white);
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  transition: color .3s ease-in-out;
}

.btn .btn-image {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  background: rgba(0,0,0,0) center/cover no-repeat;
  border-radius: 50%;
}

.btn .btn-arrow {
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--white);
  transition: background-color .3s ease-in-out;
}

.btn .btn-arrow::after {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--gradient-1);
  border-radius: 50%;
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.btn .btn-arrow .svg-ico {
  color: var(--color-25);
  transition: color .3s ease-in-out,transform .3s ease-in-out;
}

.link {
  position: relative;
  display: flex;
  align-items: center;
  width: max-content;
  padding: 0 30px 0 0;
  cursor: pointer;
}

.link:hover::after,
.link:focus-visible::after {
  transform: translateX(5px);
}

.link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 26px;
  height: 19px;
  margin: auto;
  background: url("../img/icons/big-arrow-black.svg") center no-repeat rgba(0,0,0,0);
  transition: transform .3s ease-in-out;
}

.tablet {
  display: none !important;
}

.mobile {
  display: none !important;
}

.loader::before {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: hsla(0,0%,100%,.6) url("../img/icons/spinner.svg") center/100px no-repeat;
}

.disabled,
.sold-out {
  pointer-events: none;
  background: #d8d8d8 !important;
}

.hidden {
  display: none !important;
}

.price {
  font-weight: 600;
}

.price.old-price {
  font-size: 16px;
  color: gray;
  text-decoration: line-through;
}

.swiper .swiper-slide * {
  user-select: none;
}

.slider-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.slider-nav .slider-nav__btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: rgba(0,0,0,0);
  border: 1px solid #a8b9b8;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color .3s ease-in-out,border-color .3s ease-in-out;
}

.slider-nav .slider-nav__btn:hover,
.slider-nav .slider-nav__btn:focus-visible {
  background-color: var(--color-25);
  border-color: var(--color-25);
}

.slider-nav .slider-nav__btn:hover svg,
.slider-nav .slider-nav__btn:focus-visible svg {
  color: #fff;
}

.slider-nav .slider-nav__btn.swiper-button-disabled {
  background-color: rgba(0,0,0,0);
  border-color: rgba(168,185,184,.4);
  pointer-events: none;
}

.slider-nav .slider-nav__btn.swiper-button-disabled svg {
  color: rgba(12,108,102,.5);
}

.slider-nav .slider-nav__btn.swiper-button-lock {
  display: block;
}

.slider-nav .slider-nav__btn svg {
  color: var(--color-25);
  transition: color .3s ease-in-out;
}

.slider-nav .slider-bullets {
  display: flex;
  align-items: center;
  gap: 10px;
  width: max-content;
  height: 16px;
  margin: 0 auto;
}

.slider-nav .slider-bullets .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  margin: 0;
  border: 1px solid #000;
  background-color: rgba(0,0,0,0);
  opacity: 1;
  transform: translateY(-1px);
  transition: width .3s ease-in-out,height .3s ease-in-out,background-color .3s ease-in-out;
}

.slider-nav .slider-bullets .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  background-color: #000;
}

.no-margin-y {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

.text-gradient {
  background: var(--gradient-1);
  background-clip: text;
}

.form__row-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.form__col {
  position: relative;
}

.form__col .msg {
  position: absolute;
  bottom: 0;
  left: 10px;
  display: none;
  width: max-content;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(100%);
}

.form-input,
.form-textarea {
  width: 100%;
  height: 46px;
  padding: 12px 16px;
  font-family: var(--montserrat);
  font-size: var(--font-size);
  color: var(--color-8);
  background-color: rgba(0,0,0,0);
  border: 1px solid #999;
  border-radius: var(--radius-7);
  transition: border-color .3s ease-in-out;
}

.form-input:hover,
.form-input:focus-visible,
.form-textarea:hover,
.form-textarea:focus-visible {
  border-color: var(--color-4);
}

.form-textarea {
  height: 96px;
  resize: none;
  scrollbar-width: none;
  border-radius: 20px;
}

.form-textarea::-webkit-scrollbar {
  width: 0;
}

.form-textarea:focus-visible {
  outline: 0;
}

.custom-radio {
  cursor: pointer;
}

.custom-radio:first-child {
  border-radius: 10px 0 0 10px;
}

.custom-radio:first-child .custom-radio-text,
.custom-radio:first-child .custom-radio-text::after {
  border-radius: 10px 0 0 10px;
}

.custom-radio:last-of-type {
  border-radius: 0 10px 10px 0;
}

.custom-radio:last-of-type .custom-radio-text,
.custom-radio:last-of-type .custom-radio-text::after {
  border-radius: 0 10px 10px 0;
}

.custom-radio.disabled {
  pointer-events: none;
}

.custom-radio.disabled .custom-radio-text {
  border-color: gray !important;
  opacity: .7;
  pointer-events: none;
}

.custom-radio.disabled .custom-radio-text::after {
  background-color: gray !important;
  pointer-events: none;
}

.custom-radio input {
  display: none;
}

.custom-radio input:checked~.custom-radio-text span {
  font-weight: 700;
}

.custom-radio input:checked~.custom-radio-text::after {
  opacity: 1;
}

.custom-radio .custom-radio-text {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  border: 1px solid var(--green);
  transition: font-weight .3s ease-in-out,background-color .3s ease-in-out;
}

.custom-radio .custom-radio-text::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: var(--green-gradient);
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.custom-radio .custom-radio-text span {
  position: relative;
  z-index: 1;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  user-select: none;
}

.custom-checkbox {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  cursor: pointer;
}

.custom-checkbox input {
  display: none;
}

.custom-checkbox input:checked~.custom-field::before {
  opacity: 1;
}

.custom-checkbox input.not-checked~.custom-field {
  border-color: red;
}

.custom-checkbox .custom-field {
  position: relative;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  border: 1px solid #231f20;
}

.custom-checkbox .custom-field::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 8px;
  height: 6px;
  margin: auto;
  background: rgba(0,0,0,0) url("./../img/icons/check.svg") center/cover no-repeat;
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

.custom-checkbox .custom-text {
  font-family: var(--montserrat);
  font-size: 11px;
  color: var(--color-9);
  user-select: none;
}

.custom-checkbox .custom-text a {
  color: var(--color-15);
}

.custom-checkbox .custom-text a:hover,
.custom-checkbox .custom-text a:focus-visible {
  text-decoration: underline;
}

.form-label {
  position: relative;
}

.form-label input::placeholder,
.form-label textarea::placeholder {
  font-size: 0;
}

.form-label input~label .red,
.form-label textarea~label .red {
  color: red !important;
}

.form-label input:hover~label,
.form-label textarea:hover~label {
  color: var(--color-4);
}

.form-label input:active~label,
.form-label input:focus~label,
.form-label input:not(:placeholder-shown)~label,
.form-label textarea:active~label,
.form-label textarea:focus~label,
.form-label textarea:not(:placeholder-shown)~label {
  color: var(--white) !important;
  background-color: var(--color-12);
  transform: translateY(-50%);
}

.form-label input:hover~label,
.form-label textarea:hover~label {
  color: var(--color-4);
}

.form-label label:not(.select-content__wrapper) {
  position: absolute;
  top: 0;
  left: 20px;
  display: flex;
  gap: 5px;
  padding: 5px;
  color: hsla(0,0%,100%,.7);
  background-color: rgba(0,0,0,0);
  border-radius: 6px;
  transform: translateY(37%);
  pointer-events: none;
  transition: transform .3s ease-in-out,color .3s ease-in-out,background-color .3s ease-in-out;
}

.form-label label:not(.select-content__wrapper),
.form-label label:not(.select-content__wrapper) * {
  display: flex;
  align-items: center;
  height: 26px;
  color: #999;
  line-height: 1;
  cursor: auto;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes slideInLeft {
  from {
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInLeft {
  animation-name: slideInLeft;
}

@keyframes slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  animation-name: slideInUp;
}

@keyframes slideOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(-100%, 0, 0);
  }
}

.slideOutLeft {
  animation-name: slideOutLeft;
}

@keyframes slideOutUp {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, -100%, 0);
  }
}

.slideOutUp {
  animation-name: slideOutUp;
}

@keyframes slideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(100%, 0, 0);
  }
}

.slideOutRight {
  animation-name: slideOutRight;
}

@keyframes slideInRight {
  from {
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInRight {
  animation-name: slideInRight;
}

@keyframes slideOutDown {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: translate3d(0, 100%, 0);
  }
}

.slideOutDown {
  animation-name: slideOutDown;
}

@keyframes slideInDown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInDown {
  animation-name: slideInDown;
}

@keyframes rotateAnimation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes zoomOutLeft {
  40% {
    opacity: 1;
    transform: scale3d(0.475, 0.475, 0.475) translate3d(42px, 0, 0);
  }

  to {
    opacity: 0;
    transform: scale(0.1) translate3d(-2000px, 0, 0);
  }
}

.zoomOutLeft {
  transform-origin: left center;
  animation: zoomOutLeft .5s forwards;
}

.custom-select {
  position: relative;
}

.custom-select.open .select-title {
  border-color: var(--color-4);
}

.custom-select.open .select-title .select-title__icon {
  transform: rotate(180deg);
}

.custom-select.open .select-content {
